
.page-code[data-v-4] {
  position: fixed;
  top: 50px;
  left: 0;
  right: 0;
  bottom: 0;
/*z-index: 1030;*/
}
.page-code .code-tool-bar[data-v-4] {
  height: 40px;
  border-bottom: 1px solid #d3d3d3;
  padding-top: 5px;
}
.page-code .code-editor-container[data-v-4] {
  position: absolute;
  top: 40px;
  left: 0;
  right: 0;
  bottom: 0;
}
.page-code .editor-container[data-v-4] {
  width: calc(50% - 5px);
  float: left;
  height: 100%;
}
.page-code .container-slider[data-v-4] {
  position: absolute;
  background: #c0c0c0;
  width: 5px;
  top: 0;
  bottom: 0;
  left: calc(50% - 5px);
  cursor: col-resize;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.page-code .container-slider div[data-v-4] {
  height: 50px;
  -ms-flex: 1px;
      flex: 1px;
  background: #008000;
}
.page-code .preview-container[data-v-4] {
  width: 50%;
  float: right;
  height: 100%;
  overflow-y: hidden;
}


